import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import pageView from '@/components/common-ui/page-view.vue';
import { deepClone } from '@/until/index';
import inputOptions from '@/components/common-components/input-options.vue';
// 组件
import cDefault from './component/default.vue';
let priceConfig = class priceConfig extends Vue {
    constructor() {
        super(...arguments);
        this.className = '业务配置';
        this.config = null;
    }
    updateData(updateData) {
        this.$set(this, 'config', updateData.data);
    }
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    submitHandler(e) {
        e.preventDefault();
        this.$router.push({
            name: 'businessConfig',
            params: {
                data: this.config
            }
        });
    }
    saveSchoolInfo(type, value) {
        this.$store.dispatch('updateSchoolInfo', {
            schoolId: this.schoolInfo.id,
            type: type,
            value: value
        }).then((e) => {
            if (e.success) {
                this.showToastType();
            }
            this.$store.commit('SET_SCHOOL_INFO_BY_TYPE', {
                type: type,
                value: value
            });
        });
    }
    showToastType() {
        const toast = this.$createToast({
            txt: '保存成功',
            type: 'correct'
        });
        toast.show();
    }
    initModelData() {
        let params = this.$route.params;
        if (params.type) {
            if (params.type === 'add') {
            }
            else {
                let config = deepClone(params.data);
                if (!config.funConfig) {
                    config.funConfig = {
                        buttonText: "",
                        noCost: false,
                        successText: "缴纳成功，点击下方联系我们",
                        tip: "查看工厂资料",
                        tip2: "",
                        tipImage: ""
                    };
                }
                this.$set(this, 'config', config);
            }
        }
    }
    labelUpdate(value) {
        this.$set(this.config.funConfig, value.index, value.data);
    }
    mounted() {
        this.initModelData();
    }
};
priceConfig = __decorate([
    Component({
        components: {
            page,
            pageView,
            cDefault,
            inputOptions
        }
    })
], priceConfig);
export default priceConfig;
